import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';

// Components
import Hero from '../components/Home/Hero';
import AIChatbot from '../components/Home/AIChatbot';
import ServiceCategories from '../components/Home/ServiceCategories';
import HowItWorks from '../components/Home/HowItWorks';
import Features from '../components/Home/Features';
import Testimonials from '../components/Home/Testimonials';
import EmergencyButton from '../components/Home/EmergencyButton';
import VoiceSearch from '../components/Home/VoiceSearch';
import RealTimeTracking from '../components/Home/RealTimeTracking';
import Gamification from '../components/Home/Gamification';
import CommunitySection from '../components/Home/CommunitySection';
import Pricing from '../components/Home/Pricing';
import Stats from '../components/Home/Stats';

const Home = () => {
  const navigate = useNavigate();
  const { isAuthenticated } = useAuth();
  const { emitEvent } = useSocket();
  const [showChatbot, setShowChatbot] = useState(false);
  const [showVoiceSearch, setShowVoiceSearch] = useState(false);

  // Emergency handler
  const handleEmergency = () => {
    if (!isAuthenticated) {
      navigate('/login');
      return;
    }
    
    // Get user's current location
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };
          
          // Emit emergency alert
          emitEvent('emergency-alert', {
            location,
            emergencyType: 'general',
            description: 'Emergency service requested',
          });
          
          navigate('/emergency');
        },
        (error) => {
          console.error('Error getting location:', error);
          navigate('/emergency');
        }
      );
    } else {
      navigate('/emergency');
    }
  };

  // Voice search handler
  const handleVoiceSearch = () => {
    setShowVoiceSearch(true);
  };

  // AI chatbot handler
  const handleAIChat = () => {
    setShowChatbot(true);
  };

  return (
    <div className="min-h-screen">
      {/* Hero Section */}
      <Hero 
        onEmergency={handleEmergency}
        onVoiceSearch={handleVoiceSearch}
        onAIChat={handleAIChat}
      />

      {/* AI Chatbot Modal */}
      {showChatbot && (
        <AIChatbot 
          isOpen={showChatbot}
          onClose={() => setShowChatbot(false)}
        />
      )}

      {/* Voice Search Modal */}
      {showVoiceSearch && (
        <VoiceSearch 
          isOpen={showVoiceSearch}
          onClose={() => setShowVoiceSearch(false)}
        />
      )}

      {/* Service Categories */}
      <ServiceCategories />

      {/* How It Works */}
      <HowItWorks />

      {/* Unique Features */}
      <Features />

      {/* Real-time Tracking Demo */}
      <RealTimeTracking />

      {/* Gamification */}
      <Gamification />

      {/* Community Section */}
      <CommunitySection />

      {/* Stats */}
      <Stats />

      {/* Pricing */}
      <Pricing />

      {/* Testimonials */}
      <Testimonials />

      {/* Emergency Button - Floating */}
      <EmergencyButton onClick={handleEmergency} />

      {/* Scroll to Top Button */}
      <motion.button
        className="fixed bottom-20 right-6 bg-primary-600 text-white p-3 rounded-full shadow-lg hover:bg-primary-700 transition-colors z-40"
        onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
        whileHover={{ scale: 1.1 }}
        whileTap={{ scale: 0.9 }}
        initial={{ opacity: 0, y: 100 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1 }}
      >
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
        </svg>
      </motion.button>
    </div>
  );
};

export default Home;



